import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  mode: "light",
  user: null,
  token: null,
  posts: [], //  
  challenges: [
    {
      id: "1",
      title: "100 ",
      goal: 100,
      progress: 0,
    },
    {
      id: "2",
      title: " 10 ",
      goal: 10,
      progress: 0,
    },
  ],
};

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    // ===== Theme =====
    setMode: (state) => {
      state.mode = state.mode === "light" ? "dark" : "light";
    },

    // ===== Auth =====
    setLogin: (state, action) => {
      state.user = action.payload.user;
      state.token = action.payload.token;
    },
    setLogout: (state) => {
      state.user = null;
      state.token = null;
      state.posts = [];
    },

    // ===== Friends =====
    setFriends: (state, action) => {
      if (state.user) {
        state.user.friends = action.payload.friends;
      }
    },

    // ===== Posts =====
    //    plain array,  { posts: [...], append: boolean }
    setPosts: (state, action) => {
      const incoming = action.payload;
      //   plain 
      if (Array.isArray(incoming)) {
        state.posts = incoming;
        return;
      }

      //    { posts, append }
      if (incoming && Array.isArray(incoming.posts)) {
        if (incoming.append) {
          // :  state.posts    
          if (!Array.isArray(state.posts)) state.posts = [];
          //      (   )
          state.posts = state.posts.concat(incoming.posts);
        } else {
          state.posts = incoming.posts;
        }
        return;
      }

      //      payload.post  
      //    
      state.posts = [];
    },

    //    ( _id).   ,  posts  .
    setPost: (state, action) => {
      const post = action.payload.post || action.payload; //  payload  
      if (!post) return;
      if (!Array.isArray(state.posts)) state.posts = [];
      const idx = state.posts.findIndex((p) => p._id === post._id);
      if (idx >= 0) {
        state.posts[idx] = post;
      } else {
        //       
        state.posts.unshift(post);
      }
    },

    //    id  ,  posts  
    deletePost: (state, action) => {
      const postId = action.payload.postId || action.payload;
      if (!Array.isArray(state.posts)) {
        state.posts = [];
        return;
      }
      state.posts = state.posts.filter((post) => post._id !== postId);
    },

    // ===== Challenges =====
    addChallenge: (state, action) => {
      state.challenges.push(action.payload);
    },
    updateChallengeProgress: (state, action) => {
      const { id, progress } = action.payload;
      const challenge = state.challenges.find((ch) => ch.id === id);
      if (challenge) {
        challenge.progress = progress;
      }
    },
    deleteChallenge: (state, action) => {
      state.challenges = state.challenges.filter((ch) => ch.id !== action.payload.id);
    },
  },
});

export const {
  setMode,
  setLogin,
  setLogout,
  setFriends,
  setPosts,
  setPost,
  deletePost,
  addChallenge,
  updateChallengeProgress,
  deleteChallenge,
} = authSlice.actions;

export default authSlice.reducer;
